import React, { useState, Component } from 'react';
import './style.css';
import FormData from './form';


const Landing = () => {

  const [users, setUsers] = useState([])

  const AddUsers = (name, surname, age, location) => {
    setUsers([...users, {
      uid : users.length,
      _name : name,
      _surname : surname,
      _age : age,
      _location : location
    }

    ])
  }

  return(
    <div className="container">
    <div className="con1">
      <FormData AddUsers= {AddUsers}/>
      </div>
      <div className="con2">
        <h1>Registered Students</h1>
      {users.map(action => <p> {action._name},{action._surname},{action._age},{action._location} </p>)}
      </div>

    </div>

  )
};

export default Landing
    